(function _accordion($) {
  $(".accordion .accordion-item .accordion-item-head").on("click", function() {
    $(this)
      .closest(".accordion")
      .find(".accordion-item .accordion-item-head svg #vertical")
      .stop()
      .fadeIn()
      .closest(".accordion-item")
      .find("> div")
      .stop()
      .slideUp();
    if (
      !$(this)
        .find("+ div")
        .is(":visible")
    )
      $(this)
        .find("svg #vertical")
        .stop()
        .fadeOut();
    $(this)
      .find("+ div")
      .stop()
      .slideToggle();
  });
})(jQuery);
